import { nextPage } from "../../../Utils/Functions/nextPage";
import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import style from "../../FinanceManager/FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../Loader/QvipleLoader";
import HostelFeeStructureCard from "./Structure/HostelFeeStructureCard";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import AddHostelFeeStructure from "./Structure/AddHostelFeeStructure";
import { useLocation } from "react-router-dom";
import { useHostelFinanceFeeStructureList } from "../../../Hooks/ApiHooks/HostelManager/hostel-manager-api";

const HostelFeesStructureList = ({ instituteId, fid, hostelId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();

  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [add, setAdd] = useState(false);

  const {
    hostelFinanceFeeStructureList,
    hostelFinanceFeeStructureListLoading,
    hostelFinanceFeeStructureListRefetch,
  } = useHostelFinanceFeeStructureList({
    data: {
      hid: hostelId,
      page: page,
      limit: 10,
      search: search,
      filterBy: "",
      batchBy: getQuery.state?.batchId,
    },
    skip: !hostelId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (hostelFinanceFeeStructureList?.list?.length) {
      let obj = nextPage(hostelFinanceFeeStructureList?.list?.length);
      setState(() => obj);
    }
  }, [hostelFinanceFeeStructureList?.list?.length]);

  const onClose = () => {
    setAdd((pre) => !pre);
  };

  const onSearchEvent = (e) => {
    if (
      !hostelFinanceFeeStructureListLoading &&
      hostelFinanceFeeStructureList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    hostelFinanceFeeStructureListRefetch();
  }, []);
  return (
    <>
      <div className={style.fm_header_container}>
        <h6>{getQuery.state?.batchName ?? ""}</h6>
        <div
          className={style.fm_add_button}
          style={{
            alignItems: "center",
          }}
        >
          <CreateButton
            label="add_new_fees_structure"
            customStyle={{
              marginBlock: "0.5rem",
              padding: "0.4rem 1rem",
            }}
            onAction={onClose}
          />
        </div>
      </div>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.7rem",
        }}
      >
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </div>
        </section>
      </div>

      {hostelFinanceFeeStructureList?.list?.map((structure, index) =>
        hostelFinanceFeeStructureList?.list?.length === index + 1 ? (
          <div key={structure?._id} ref={ref}>
            <HostelFeeStructureCard
              structure={structure}
              instituteId={instituteId}
              fid={fid}
              hostelId={hostelId}
              onRefetch={onRefetch}
            />
          </div>
        ) : (
          <HostelFeeStructureCard
            structure={structure}
            key={structure?._id}
            instituteId={instituteId}
            fid={fid}
            hostelId={hostelId}
            onRefetch={onRefetch}
          />
        )
      )}

      {hostelFinanceFeeStructureListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
        )
      ) : search ? (
        hostelFinanceFeeStructureList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No fee structure found related this search."
          />
        )
      ) : (
        hostelFinanceFeeStructureList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No fee structure added."
          />
        )
      )}
      {add && (
        <AddHostelFeeStructure
          instituteId={instituteId}
          fid={fid}
          onClose={onClose}
          onRefetch={onRefetch}
          hostelId={hostelId}
          isRefetch
        />
      )}
    </>
  );
};

export default HostelFeesStructureList;
