// import UploadExcelButton from "../../../../Ui/Button/UploadExcelButton";
import style from "../FeesStructure.module.css";
import styleFinance from "../../FinanceManager.module.css";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useEffect, useState } from "react";
import { useFileUpload } from "../../../../Hooks/ApiHooks/Auth/auth-api";
import {
  // useFinanceAddExcelFeeHead,
  useFinanceAllOtherFeeHeadMaster,
} from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import OtherFeeMasterCard from "./OtherFeeMasterCard";
import AddFeesHead from "./AddFeesHead";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
const OtherFeeMaster = ({ fid }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [addCategory, setAddCategory] = useState(false);
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  // const [fileUpload] = useFileUpload();
  // const [financeAddExcelFeeHead] = useFinanceAddExcelFeeHead();
  const {
    financeAllOtherFeeHeadMaster,
    financeAllOtherFeeHeadMasterLoading,
    financeAllOtherFeeHeadMasterRefetch,
  } = useFinanceAllOtherFeeHeadMaster({
    data: {
      fid: fid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !fid,
  });

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (financeAllOtherFeeHeadMaster?.list?.length) {
      let obj = nextPage(financeAllOtherFeeHeadMaster?.list?.length);
      setState(() => obj);
    }
  }, [financeAllOtherFeeHeadMaster?.list?.length]);
  const onSearchEvent = (e) => {
    if (
      !financeAllOtherFeeHeadMasterLoading &&
      financeAllOtherFeeHeadMaster?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };

  // const onExcelFileUpload = (e) => {
  //   if (e.target.files[0]) {
  //     setDisabled((pre) => !pre);
  //     const formData = new FormData();
  //     formData.append("file", e.target.files[0]);
  //     fileUpload({
  //       fileUpload: formData,
  //     })
  //       .then((res) => {
  //         if (res.data?.imageKey) {
  //           financeAddExcelFeeHead({
  //             fid: fid,
  //             excelFile: {
  //               excel_file: res.data?.imageKey,
  //             },
  //           })
  //             .then(() => {
  //               setDisabled((pre) => !pre);
  //             })
  //             .catch({});
  //         } else {
  //           setDisabled((pre) => !pre);
  //         }
  //       })
  //       .catch({});
  //   }
  // };

  const onToggle = () => {
    setAddCategory((pre) => !pre);
  };
  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    financeAllOtherFeeHeadMasterRefetch();
  };
  return (
    <>
      <div
        className={styleFinance.fm_search_full}
        style={{
          marginBottom: "0.5rem",
        }}
      >
        <section className={styleFinance.fm_search_full_container}>
          <div
            className={styleFinance.fm_search_full_container_inner}
            style={{
              flex: "1.5 1.5",
            }}
          >
            <img
              className={styleFinance.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </div>
          <div
            className={style.upload_excel_container}
            style={{
              flex: "1 1",
            }}
          >
            {/* <UploadExcelButton onExcelFileUpload={onExcelFileUpload} /> */}
            <CreateButton
              label="add_other_fee_head"
              customStyle={{
                paddingBlock: "0.4rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          </div>
        </section>
      </div>

      {financeAllOtherFeeHeadMaster?.list?.map((feeHead, index) =>
        financeAllOtherFeeHeadMaster?.list?.length === index + 1 ? (
          <div key={feeHead?._id} ref={ref}>
            <OtherFeeMasterCard
              feeHead={feeHead}
              fid={fid}
              onRefetch={onRefetch}
              isFlow="OTHER"
            />
          </div>
        ) : (
          <OtherFeeMasterCard
            feeHead={feeHead}
            key={feeHead?._id}
            fid={fid}
            onRefetch={onRefetch}
            isFlow="OTHER"
          />
        )
      )}
      {financeAllOtherFeeHeadMasterLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        financeAllOtherFeeHeadMaster?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No other fee master found related this search."
          />
        )
      ) : (
        financeAllOtherFeeHeadMaster?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No other fee master added."
          />
        )
      )}
      {disabled && <QvipleLoader />}
      {addCategory && (
        <AddFeesHead
          onClose={onToggle}
          fid={fid}
          onRefetch={onRefetch}
          isFlow="OTHER"
        />
      )}
    </>
  );
};

export default OtherFeeMaster;
