import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useLocation, useSearchParams } from "react-router-dom";
import HostelBatchList from "./HostelBatchList";
import HostelFeesStructureList from "./HostelFeesStructureList";
import AddHostelFeeStructure from "./Structure/AddHostelFeeStructure";
import CreateButton from "../../../Ui/Button/CreateButton";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../FinanceManager/FeesStructure/FeesStructure.module.css";
const HostelFeeStructureConfig = ({ instituteId, hostelId, fid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const [add, setAdd] = useState(false);

  const onClose = () => {
    setAdd((pre) => !pre);
  };

  return (
    <>
      {!searchParams.get("u") ? null : (
        <div
          className={style.structure_container_title}
          style={{
            paddingInline: "0",
          }}
        >
          <h6>{t("hostel_batch")}</h6>
          <div className={style.upload_excel_container}>
            <CreateButton
              label="add_new_fees_structure"
              customStyle={{
                marginBlock: "0.5rem",
                padding: "0.4rem 1rem",
              }}
              onAction={onClose}
            />
          </div>
        </div>
      )}
      <BorderBottom />

      {!searchParams.get("u") ? (
        <HostelFeesStructureList
          fid={fid}
          instituteId={instituteId}
          hostelId={hostelId}
        />
      ) : searchParams.get("u") === "batch" ? (
        <HostelBatchList
          carryParentState={getQuery?.state}
          hostelId={hostelId}
          instituteId={instituteId}
        />
      ) : null}
      {add && (
        <AddHostelFeeStructure
          instituteId={instituteId}
          fid={fid}
          hostelId={hostelId}
          onClose={onClose}
        />
      )}
    </>
  );
};

export default HostelFeeStructureConfig;
