import { useEffect, useState } from "react";
import style from "../../../FinanceManager/FeesStructure/FeesStructure.module.css";
import FormInput from "../../../../Ui/Input/FormInput";
import CustomSelectBatch from "../../../../Ui/Select/CustomSelectBatch";
import CustomSelectCategory from "../../../../Ui/Select/CustomSelectCategory";
import CustomSelectStandard from "../../../../Ui/Select/CustomSelectStandard";
import {
  useFinanceAllFeesCategory,
  useFinanceFeeStructureCreateValidate,
} from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import Modal from "../../../../Ui/Modal/Modal";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { useTranslation } from "react-i18next";
import SetButton from "../../../../Ui/Button/SetButton";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import NotifyEvent from "../../../../Utils/Snackbar/NotifyEvent";
import CustomSelectUnit from "../../../../Ui/Select/CustomSelectUnit";
import {
  useHostelBatchList,
  useHostelStandardList,
  useHostelUnitList,
} from "../../../../Hooks/ApiHooks/HostelManager/hostel-manager-api";

const HostelFeeStructureForm = ({
  onClose,
  fid,
  structure,
  setStructure,
  setStep,
  hostelId,
  instituteId,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    structure_name: structure?.structure_name ?? "",
    category_master: structure?.category_master ?? "",
    class_master: structure?.class_master ?? "",
    batch_master: structure?.batch_master ?? "",
    unit_master: structure?.unit_master ?? "",
  });
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  const [financeFeeStructureCreateValidate] =
    useFinanceFeeStructureCreateValidate();
  useEffect(() => {
    if (structure?.structure_name) {
      setState((prev) => ({
        ...prev,
        structure_name: structure?.structure_name ?? "",
        category_master: structure?.category_master ?? "",
        class_master: structure?.class_master ?? "",
        batch_master: structure?.batch_master ?? "",
        unit_master: structure?.unit_master ?? "",
      }));
    }
  }, [structure?.structure_name]);

  const { hostelBatchList } = useHostelBatchList({
    data: {
      hid: hostelId,
      page: 1,
      limit: 1000,
      search: "",
      id: instituteId,
    },
    skip: !hostelId ? !hostelId : !instituteId,
  });

  const { hostelStandardList } = useHostelStandardList({
    data: {
      hid: hostelId,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !hostelId,
  });

  const { financeAllFeesCategory } = useFinanceAllFeesCategory({
    fid: fid,
    skip: !fid,
  });
  const { hostelUnitList } = useHostelUnitList({
    data: {
      hid: hostelId,
      page: 1,
      limit: 1000,
      search: "",
    },
    skip: !hostelId,
  });

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      structure_name: e.target.value,
    }));
  };
  const onBatchSelect = (value) => {
    setState((prev) => ({
      ...prev,
      batch_master: value,
    }));
  };

  const onCategorySelect = (value) => {
    setState((prev) => ({
      ...prev,
      category_master: value,
    }));
  };
  const onUnitSelect = (value) => {
    setState((prev) => ({
      ...prev,
      unit_master: value,
    }));
  };

  const onStandardSelect = (value) => {
    setState((prev) => ({
      ...prev,
      class_master: value,
    }));
  };

  const onSetFeeStructure = () => {
    let validation = requiredField(state);
    if (existingRequiredField(validation)) {
      // setDisabled((pre) => !pre);
      // financeFeeStructureCreateValidate({
      //   validate: {
      //     category_master: state?.category_master?._id,
      //     class_master: state?.class_master?._id,
      //     batch_master: state?.batch_master?._id,
      //     unit_master: structure?.unit_master?._id,
      //   },
      // })
      //   .then((res) => {
      //     setDisabled((pre) => !pre);
      //     if (res?.data?.avail_struct) {
      //       setSnackbar({
      //         msg: res?.data?.message,
      //         open: true,
      //         variant: "error",
      //       });
      //       setTimeout(() => {
      //         onClose();
      //       }, [3000]);
      //     } else {
      setStructure((prev) => ({
        ...prev,
        ...state,
      }));
      setStep(1);
      //   }
      // })
      // .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("add_new_fees_structure")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("fee_structure_name")}
              placeholder={t("fee_structure_name_placeholder")}
              type="text"
              name={"structure_name"}
              value={state.structure_name}
              onChange={onInputChange}
              errorShow={errorField["structure_name"]}
            />
            <CustomSelectBatch
              selectLabel={t("select_batch")}
              selectedValue={t("select_batch_placeholder")}
              options={
                hostelBatchList?.list?.length > 0 ? hostelBatchList?.list : []
              }
              onClick={onBatchSelect}
              viewAs="NORMAL_FILTER"
              defalutValue={state.batch_master?.batchName}
              errorShow={errorField["batch_master"]}
              isFull
            />
            <CustomSelectCategory
              selectLabel={t("select_fee_category_master")}
              selectedValue={t("please_select_fees_category")}
              options={
                financeAllFeesCategory?.length > 0 ? financeAllFeesCategory : []
              }
              onClick={onCategorySelect}
              viewAs="NORMAL_FILTER"
              defalutValue={state.category_master?.category_name}
              errorShow={errorField["category_master"]}
              isFull
            />
            <CustomSelectUnit
              selectLabel={t("select_unit")}
              selectedValue={t("select_unit_placeholder")}
              options={
                hostelUnitList?.list?.length > 0 ? hostelUnitList?.list : []
              }
              onClick={onUnitSelect}
              viewAs="NORMAL_FILTER"
              defalutValue={state.unit_master?.hostel_unit_name}
              errorShow={errorField["unit_master"]}
              isFull
            />
            <CustomSelectStandard
              selectLabel={t("select_standard")}
              selectedValue={t("please_select_the_standard_master")}
              defalutValue={state.class_master?.className}
              onClick={onStandardSelect}
              name="class_master"
              options={
                hostelStandardList?.list?.length > 0
                  ? hostelStandardList?.list
                  : []
              }
              errorShow={errorField["class_master"]}
              viewAs="NORMAL_FILTER"
              isFull
            />

            <SetButton label="set_fee_structure" onAction={onSetFeeStructure} />
          </UserMemberContentWrapper>
          <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
        </div>
      </Modal>

      {disabled && <QvipleLoader />}
    </>
  );
};

export default HostelFeeStructureForm;
